.title {
    width: 80%;
    margin: 0 auto;

    font-family: $opsans;
    color: $textcolor;
    font-size: 25px;
    font-weight: 600;
    text-align: center;
}

@media (max-width: 768px) {
	.title {
		font-size: 18px;
	}
}

@media (max-width: 480px) {
	.title {
		font-size: 12px;
	}
}
